export async function getUserProfile(userId: string): Promise<{ user: User; joinedEvents: Event[] } | null> {
  try {
    const user = await getUserById(userId);
    if (!user) {
      console.warn(`用户个人资料未找到: ${userId}`);
      return null;
    }
    const joinedEvents = await getEventsForUser(userId);
    return { user: {...user}, joinedEvents: joinedEvents.map(e => ({...e})) };
  } catch (error) {
    console.error(`获取用户 ${userId} 个人资料失败:`, error);
    return null;
  }
}

