export async function getUsersAction(): Promise<User[]> {
  const currentUser = await getSessionUser();
  if (!currentUser || currentUser.op !== 1) {
    console.warn("非管理员尝试获取用户列表。");
    return [];
  }
  return apiGetUsers();
}

const AdminAddUserFormSchema = z.object({
  minecraftUsername: MinecraftUsernameSchema,
  avatarUrl: z.string().url("无效的头像链接。").optional().or(z.literal('')),
  op: z.enum(['0', '1']).transform(Number),
  password: z.string().min(6, "密码至少需要6个字符。"),
  confirmPassword: z.string(),
  adminConfirmPassword: z.string().min(1, "需要您的密码来确认此操作。"),
  qqNumber: QQNumberSchema.optional().or(z.literal('')),
}).refine(data => data.password === data.confirmPassword, {
  message: "两次输入的密码不匹配。",
  path: ["confirmPassword"],
});


export async function adminAddUserAction(prevState: AdminAddUserFormState, formData: FormData): Promise<AdminAddUserFormState> {
  try {
    const performingUser = await getSessionUser();
    if (!performingUser || performingUser.op !== 1) {
      return { message: "您没有权限执行此操作。", success: false, errors: { form: ["权限不足。"] } };
    }

    const rawFormData = Object.fromEntries(formData.entries());
    const validatedFields = AdminAddUserFormSchema.safeParse(rawFormData);

    if (!validatedFields.success) {
      return {
        message: '输入无效。请检查您填写的信息。',
        errors: validatedFields.error.flatten().fieldErrors,
        success: false,
      };
    }

    const { minecraftUsername, avatarUrl, op, password, adminConfirmPassword, qqNumber } = validatedFields.data;

    if (performingUser.password !== adminConfirmPassword) {
        return { message: '您的账户密码不正确，无法授权此操作。', errors: { adminConfirmPassword: ['管理员密码不正确。'] }, success: false };
    }
    
    const isNameMCValid = await checkNameMC(minecraftUsername);
    if (!isNameMCValid) {
      return { message: '此 Minecraft 用户名在 NameMC 上无效或无法验证。', errors: { minecraftUsername: ['此 Minecraft 用户名在 NameMC 上无效或无法验证。'] }, success: false };
    }

    const existingUserByUsername = await getUserByUsername(minecraftUsername);
    if (existingUserByUsername) {
      return { message: '此 Minecraft 用户名已被注册。', errors: { minecraftUsername: ['此 Minecraft 用户名已被注册。'] }, success: false };
    }

    if (qqNumber && qqNumber.trim() !== "") {
        const existingUserByQQ = await getUserByQQNumber(qqNumber);
        if (existingUserByQQ) {
          return { message: '此 QQ 号码已被其他用户注册。', errors: { qqNumber: ['此 QQ 号码已被其他用户注册。'] }, success: false };
        }
      }

    await apiCreateUser({
      id: minecraftUsername,
      username: minecraftUsername,
      avatarUrl: avatarUrl || `https://placehold.co/100x100.png?text=${minecraftUsername.substring(0,2).toUpperCase()}`,
      op: op,
      password: password, 
      qqNumber: qqNumber || undefined,
    });

    return { message: `用户 ${minecraftUsername} 已成功创建。`, success: true };

  } catch (error: any) {
    console.error("Admin Add User Action - Server Error:", error);
    return { message: '创建用户时发生服务器内部错误。', success: false, errors: { form: ['创建用户时发生服务器内部错误。'] } };
  }
}


const AdminUpdateUserFormSchema = z.object({
  userIdToUpdate: z.string().min(1, "用户ID是必需的。"),
  minecraftUsername: MinecraftUsernameSchema,
  avatarUrl: z.string().url("无效的头像链接。").optional().or(z.literal('')),
  op: z.enum(['0', '1']).transform(Number),
  newPassword: z.string().min(6, "新密码至少需要6个字符。").optional().or(z.literal('')),
  confirmNewPassword: z.string().optional().or(z.literal('')),
  adminConfirmPassword: z.string().min(1, "需要您的密码来确认更改。"),
  qqNumber: QQNumberSchema.optional().or(z.literal('')),
}).refine(data => {
  if (data.newPassword && data.newPassword !== data.confirmNewPassword) {
    return false;
  }
  return true;
}, {
  message: "两次输入的新密码不匹配。",
  path: ["confirmNewPassword"],
});


export async function adminUpdateUserAction(prevState: AdminUpdateUserFormState, formData: FormData): Promise<AdminUpdateUserFormState> {
   try {
    const performingUser = await getSessionUser();
    if (!performingUser || performingUser.op !== 1) {
      return { message: "您没有权限执行此操作。", success: false, errors: { form: ["权限不足。"] } };
    }

    const rawFormData = Object.fromEntries(formData.entries());
    const validatedFields = AdminUpdateUserFormSchema.safeParse(rawFormData);

    if (!validatedFields.success) {
      return {
        message: '输入无效。请检查您填写的信息。',
        errors: validatedFields.error.flatten().fieldErrors,
        success: false,
      };
    }
    
    const { userIdToUpdate, minecraftUsername, avatarUrl, op, newPassword, adminConfirmPassword, qqNumber } = validatedFields.data;

    if (performingUser.password !== adminConfirmPassword) {
        return { message: '您的账户密码不正确，无法授权此更改。', errors: { adminConfirmPassword: ['管理员密码不正确。'] }, success: false };
    }

    const userToUpdate = await getUserById(userIdToUpdate);
    if (!userToUpdate) {
      return { message: '要更新的用户未找到。', errors: { form: ['用户未找到。'] }, success: false };
    }
    
    if (minecraftUsername.toLowerCase() !== userToUpdate.username.toLowerCase()) {
      const existingUserWithNewName = await getUserByUsername(minecraftUsername);
      if (existingUserWithNewName && existingUserWithNewName.id !== userIdToUpdate) {
        return { message: '此 Minecraft 用户名已被其他用户注册。', errors: { minecraftUsername: ['此 Minecraft 用户名已被其他用户注册。'] }, success: false };
      }
      const isNameMCValid = await checkNameMC(minecraftUsername);
      if (!isNameMCValid) {
        return { message: '新的 Minecraft 用户名在 NameMC 上无效或无法验证。', errors: { minecraftUsername: ['新的 Minecraft 用户名在 NameMC 上无效或无法验证。'] }, success: false };
      }
    }

    if (qqNumber && qqNumber.trim() !== "" && qqNumber !== userToUpdate.qqNumber) {
        const existingUserByQQ = await getUserByQQNumber(qqNumber);
        if (existingUserByQQ && existingUserByQQ.id !== userIdToUpdate) {
          return { message: '此 QQ 号码已被其他用户注册。', errors: { qqNumber: ['此 QQ 号码已被其他用户注册。'] }, success: false };
        }
    }
    
    const updateData: Partial<User> = {
      username: minecraftUsername,
      avatarUrl: avatarUrl || userToUpdate.avatarUrl,
      op: (performingUser.id === userIdToUpdate && op !== performingUser.op) ? performingUser.op : op, // Prevent admin from de-opping themselves unless intended by another admin
      qqNumber: qqNumber === '' ? undefined : (qqNumber || userToUpdate.qqNumber),
    };

    if (newPassword) {
      updateData.password = newPassword; 
    }

    await apiUpdateUserById(userIdToUpdate, updateData);

    return { message: `用户 ${minecraftUsername} (ID: ${userIdToUpdate}) 已成功更新。`, success: true };

  } catch (error: any) {
    console.error("Admin Update User Action - Server Error:", error);
    return { message: '更新用户时发生服务器内部错误。', success: false, errors: { form: ['更新用户时发生服务器内部错误。'] } };
  }
}

export async function adminDeleteUserAction(userIdToDelete: string, adminConfirmPassword: string): Promise<{ success: boolean; message: string }> {
  const performingUser = await getSessionUser();
  if (!performingUser || performingUser.op !== 1) {
    return { success: false, message: "您没有权限执行此操作。" };
  }
  if (performingUser.id === userIdToDelete) {
    return { success: false, message: "您不能删除自己的账户。" };
  }
  if (performingUser.password !== adminConfirmPassword) {
    return { success: false, message: "您的账户密码不正确，无法授权删除操作。" };
  }

  try {
    const result = await apiDeleteUserById(userIdToDelete);
    return result;
  } catch (error) {
    console.error("Admin Delete User Action - Server Error:", error);
    const errorMessage = error instanceof Error ? error.message : "删除用户时发生服务器内部错误。";
    return { success: false, message: errorMessage };
  }
}
